import React, { PureComponent } from 'react';
import classnames from 'classnames';
import config from '../../_util/config';
import FormItem from './FormItem';
import create from './create';
import { FormHOC } from './context';
import { isFunction } from '../../_util/typeof';
export default class Form extends PureComponent {
    constructor(props) {
        super(props);
        this.state = {};
    }
    render() {
        const { prefixCls, className, children, onSubmit } = this.props;
        if (!children) {
            return null;
        }
        return (<FormHOC.Consumer>
                {(data) => {
            return <form ref={(formComponent) => {
                if (data && isFunction(data.getFormComponent)) {
                    data.getFormComponent(formComponent);
                }
            }} onSubmit={(e) => {
                if (onSubmit && isFunction(onSubmit)) {
                    if (data && isFunction(data.formOnSubmit)) {
                        const { errList, outputData } = data.formOnSubmit();
                        onSubmit(errList, outputData);
                    }
                    e.preventDefault();
                }
            }} className={classnames(prefixCls, className)}>
                        {children}
                    </form>;
        }}
            </FormHOC.Consumer>);
    }
}
Form.defaultProps = {
    prefixCls: `${config.cls}-form`,
    className: '',
    onSubmit: null
};
Form.create = create;
Form.Item = FormItem;



