import React, { PureComponent } from 'react';
import classNames from 'classnames';
import config from '../../_util/config';
export default class CardBody extends PureComponent {
    constructor(props) {
        super(props);
    }
    render() {
        const { style, prefixCls, children, className } = this.props;
        const cardClassName = classNames(prefixCls, className);
        return (<div style={style} className={cardClassName}>
                {children}
            </div>);
    }
}
CardBody.defaultProps = {
    prefixCls: `${config.cls}-card2-body`,
    className: '',
    style: {},
};



