import React, { PureComponent } from 'react';
import Icon from '../../Icon';
import Card from '../../Card';
import Popup from '../../Popup';
import Alert from '../../Alert';
import classnames from 'classnames';
export default class PhoneNumberPrefix extends PureComponent {
    constructor(props) {
        super(props);
        this.prefixClass = 'zds-input-phone';
        this.state = {
            openMorePhonePrefix: false
        };
        this.openMorePhonePrefix = this.openMorePhonePrefix.bind(this);
    }
    openMorePhonePrefix() {
        this.setState({
            openMorePhonePrefix: true
        });
    }
    selectPrefix(item) {
        if (item.id == this.props.currPrefix)
            return;
        this.setState({
            openMorePhonePrefix: false
        });
        if (item.id !== 'other') {
            this.props.onChange(item);
        }
        else {
            this.showAlert();
        }
    }
    showAlert() {
        const { lang } = this.props;
        let codeInput;
        Alert({
            title: lang == 'cn' ? '' : '',
            content: (<div className={`${this.prefixClass}-code-input-box`}>
                    <span className={`${this.prefixClass}-code-input-box-add`}>+</span>
                    <input className={`${this.prefixClass}-code-input-box-input`} pattern='\\d*' onKeyUp={(e) => {
                codeInput.value = e.currentTarget.value.replace(/[+]/g, '');
            }} ref={el => codeInput = el}/>
                </div>),
            buttons: [
                {
                    text: lang == 'cn' ? '' : '',
                    onPress: () => {
                    },
                    props: {
                        type: 'special',
                        style: { color: '#696E7C', border: 'none' }
                    }
                },
                {
                    text: lang == 'cn' ? '' : '',
                    onPress: () => {
                        codeInput.value != '' && this.props.onChange({
                            id: 'other',
                            detail: `+${codeInput.value}`
                        });
                    },
                    props: {
                        type: 'special',
                        style: { color: 'rgba(40, 113, 247, 1)', border: 'none' }
                    }
                }
            ]
        });
    }
    render() {
        const { children, currPrefix, phonePrefixList, lang } = this.props;
        const { openMorePhonePrefix } = this.state;
        return (<div className={`${this.prefixClass}-box`}>
                <div className={`${this.prefixClass}-prefix`} onClick={this.openMorePhonePrefix}>
                    {currPrefix}
                    <Icon type='arrows' style={{ width: '14px', height: '14px' }}/>
                </div>
                {children}
                <Popup maskClose visible={openMorePhonePrefix} onClose={() => {
            this.setState({
                openMorePhonePrefix: false
            });
        }}>
                    <Card>
                        <Card.Header className={`${this.prefixClass}-popup-header`} title={lang == 'cn' ? '' : ''}/>
                        <div className={`${this.prefixClass}-popup-box-list`}>
                            {phonePrefixList && phonePrefixList.map((item, key) => {
            const className = classnames(`${this.prefixClass}-popup-prefix-item`, { 'active': item.id == currPrefix });
            return (<Card.Body key={`${item.id}-${key}`}>
                                            <div className={className} onClick={() => { this.selectPrefix(item); }}>
                                                <p>{item.detail}</p>
                                                {item.id == currPrefix && <Icon size='sm' type='success_fill'/>}
                                            </div>
                                        </Card.Body>);
        })}
                        </div>
                    </Card>
                </Popup>
            </div>);
    }
}



