export default function createCalendarMap(lang, dateExtension, startInfo, endInfo, yesterday, i18n) {
    const date = new Date();
    let year = date.getFullYear();
    let month = date.getMonth();
    const now = new Date();
    const calendarMap = [];
    // 
    const nowMonth = now.getMonth();
    const nowYear = now.getFullYear();
    const nowDay = now.getDate();
    let startIndexInfo = null;
    let endIndexInfo = null;
    const step = 13;
    for (let i = 0; i < step; i++) {
        const { monthData, startIndex, endIndex } = _createMonthMap({
            n_y: nowYear,
            n_m: nowMonth,
            n_d: nowDay,
        }, year, month, lang, dateExtension, startInfo, endInfo, startIndexInfo, endIndexInfo, yesterday, i18n);
        startIndexInfo = startIndex;
        endIndexInfo = endIndex;
        calendarMap.push(monthData);
        if (month >= 11) {
            month = 0;
            year += 1;
        }
        else {
            month += 1;
        }
    }
    // startendnull
    if (!startIndexInfo || !endIndexInfo) {
        startIndexInfo = null;
        endIndexInfo = null;
    }
    return {
        startIndexInfo,
        endIndexInfo,
        calendarMap
    };
}
function _createMonthMap(now, year, month, lang, dateExtension, startInfo, endInfo, startIndexInfo, endIndexInfo, yesterday, i18n) {
    const startDay = 0;
    const lastDay = new Date(year, month + 1, 0).getDate();
    const monthList = [];
    // 
    let effectiveDate;
    if (year <= now.n_y && month <= now.n_m) {
        effectiveDate = now.n_d;
    }
    let rowList = [];
    // 1
    rowList = _setStartEmptyItem(year, month, startDay + 1);
    for (let i = startDay; i < lastDay; i++) {
        let col;
        const currData = i + 1;
        if (effectiveDate && currData < effectiveDate) {
            col = rowList.push(_getDayItemInfo({
                day: currData,
                month,
                year,
                gone: yesterday && year == now.n_y && month == now.n_m && currData == now.n_d - 1 ? false : true,
                sub: _createDayInfoSubText(Object.assign({}, now, { c_y: year, c_m: month, c_d: currData }), i18n)
            }, dateExtension));
        }
        else {
            col = rowList.push(_getDayItemInfo({
                day: currData,
                month,
                year,
                gone: false,
                sub: _createDayInfoSubText(Object.assign({}, now, { c_y: year, c_m: month, c_d: currData }), i18n)
            }, dateExtension));
        }
        // 71
        if (rowList.length % 7 == 0) {
            monthList.push(rowList.splice(0, 7));
        }
        // 
        if (startInfo && endInfo && (startIndexInfo == null || endIndexInfo == null)) {
            if (year == startInfo.Y && month == startInfo.M && currData == startInfo.D) {
                startIndexInfo = { year, monthKey: month, rowKey: monthList.length, itemKey: col - 1 };
            }
            if (year == endInfo.Y && month == endInfo.M && currData == endInfo.D) {
                endIndexInfo = { year, monthKey: month, rowKey: monthList.length, itemKey: col - 1 };
            }
        }
    }
    // 
    // rowListlength0
    rowList = rowList.length == 0 ? rowList : rowList.concat(_setEndEmptyItem(year, month, rowList.length));
    monthList.push(rowList.splice(0, 7));
    return {
        monthData: {
            title: _createMonthTitle(month, lang),
            m: month,
            y: year,
            list: monthList,
        },
        endIndex: endIndexInfo,
        startIndex: startIndexInfo
    };
}
function _setStartEmptyItem(year, month, day) {
    const date = new Date(`${year}/${month + 1}/${day} 00:00:00`);
    const week = date.getDay() - 1; // 
    const emptyList = [];
    for (let i = 0; i < week; i++) {
        emptyList.push(_getDayItemInfo({
            year,
            month,
            empty: true
        }));
    }
    return emptyList;
}
function _setEndEmptyItem(year, month, length) {
    const emptyList = [];
    for (let i = 7; i > length; i--) {
        emptyList.push(_getDayItemInfo({
            year,
            month,
            empty: true
        }));
    }
    return emptyList;
}
function _dateExtensionMerge(dataInfo, dateExtension) {
    const defaultItemInfo = {
        empty: dataInfo.empty || false,
        gone: dataInfo.gone || false,
        y: dataInfo.year,
        m: dataInfo.month,
        d: dataInfo.day,
        main: dataInfo.day,
        sub: dataInfo.sub || null
    };
    const extensionItem = dateExtension[`${defaultItemInfo.y}/${defaultItemInfo.m + 1}/${defaultItemInfo.d}`];
    if (extensionItem) {
        defaultItemInfo.sub = extensionItem.sub || defaultItemInfo.sub;
        defaultItemInfo.main = extensionItem.date || defaultItemInfo.main;
        if (extensionItem.date) {
            defaultItemInfo.extensionMain = true;
        }
    }
    return defaultItemInfo;
}
function _getDayItemInfo(dataInfo, dateExtension = null) {
    if (!dateExtension) {
        return {
            empty: dataInfo.empty || false,
            gone: dataInfo.gone || false,
            y: dataInfo.year,
            m: dataInfo.month,
            d: dataInfo.day,
            main: dataInfo.day,
            sub: dataInfo.sub
        };
    }
    else {
        return _dateExtensionMerge(dataInfo, dateExtension);
    }
}
function _createMonthTitle(month, lang) {
    switch (month) {
        case 0: return lang == 'cn' ? '' : '';
        case 1: return '';
        case 2: return '';
        case 3: return '';
        case 4: return '';
        case 5: return '';
        case 6: return '';
        case 7: return '';
        case 8: return '';
        case 9: return '';
        case 10: return lang == 'cn' ? '' : '';
        case 11: return '';
    }
    return '';
}
function _createDayInfoSubText(data, i18n) {
    if (data.n_y == data.c_y && data.n_m == data.c_m && data.n_d == data.c_d) {
        return i18n.today;
    }
    return null;
}



