import React, { PureComponent } from 'react';
import config from '../../_util/config';
import Icon from '../../Icon';
export default class CalendarCloseBox extends PureComponent {
    constructor(props) {
        super(props);
    }
    render() {
        const { prefixCls, onClose } = this.props;
        return (<div className={`${prefixCls}-close-box`}>
                <div className={`${prefixCls}-close-btn`} onClick={() => { onClose(); }}>
                    <Icon type='error' size='xs'/>
                </div>
            </div>);
    }
}
CalendarCloseBox.defaultProps = {
    prefixCls: `${config.cls}-calendar`,
};



