import React, { PureComponent } from 'react';
import classNames from 'classnames';
import config from '../../_util/config';
export default class SkeletonItem extends PureComponent {
    constructor(props) {
        super(props);
    }
    render() {
        const { style, prefixCls, className, height, width } = this.props;
        const selectClassName = classNames(prefixCls, className);
        return (<div style={Object.assign({}, style, { height: height, width: width })} className={selectClassName}/>);
    }
}
SkeletonItem.defaultProps = {
    prefixCls: `${config.cls}-skeleton-item`,
    className: '',
    style: {},
    height: null,
    width: null
};



