import React, { PureComponent } from 'react';
import config from '../../_util/config';
export default class CalendarWeek extends PureComponent {
    constructor(props) {
        super(props);
    }
    render() {
        const { prefixCls, weekList } = this.props;
        return (<div className={`${prefixCls}-week-box`}>
                {weekList.map((item, key) => {
            return (<div className='item' key={key}>
                            <p>{item}</p>
                        </div>);
        })}
            </div>);
    }
}
CalendarWeek.defaultProps = {
    prefixCls: `${config.cls}-calendar`,
};



