import React, { PureComponent } from 'react';
import classNames from 'classnames';
import config from '../../_util/config';
import Box from './SkeletonBox';
import Item from './SkeletonItem';
export default class Skeleton extends PureComponent {
    constructor(props) {
        super(props);
    }
    render() {
        const { style, prefixCls, className, children } = this.props;
        const selectClassName = classNames(prefixCls, className);
        return (<div style={style} className={selectClassName}>
                {children}
            </div>);
    }
}
Skeleton.defaultProps = {
    prefixCls: `${config.cls}-skeleton`,
    className: '',
    style: {},
};
Skeleton.Box = Box;
Skeleton.Item = Item;



