import React, { PureComponent } from 'react';
import classNames from 'classnames';
import config from '../../_util/config';
export default class ButtonGroup extends PureComponent {
    constructor(props) {
        super(props);
    }
    render() {
        const { children, prefixCls, className, style, direction } = this.props;
        const btnGroupClass = classNames(prefixCls, className, `${prefixCls}-${direction}`);
        return (<div style={style} className={btnGroupClass}>
                {children}
            </div>);
    }
}
ButtonGroup.defaultProps = {
    prefixCls: `${config.cls}-button-group`,
    className: '',
    direction: 'center',
    style: {}
};



