import React, { PureComponent } from 'react';
import classNames from 'classnames';
import config from '../../_util/config';
import Popup from '../../Popup';
import List from '../../List';
import Icon from '../../Icon';
import { isFunction } from '../../_util/typeof';
export default class Select extends PureComponent {
    constructor(props) {
        super(props);
        this.state = {
            visible: false,
        };
        this.clickItem = this.clickItem.bind(this);
    }
    componentDidMount() {
        if (this.props.isShow) {
            this.togglePopup(true);
        }
    }
    componentWillReceiveProps(nextProps) {
        if (nextProps.isShow) {
            this.togglePopup(true);
        }
        else if (!nextProps.isShow && this.props.isShow) {
            this.togglePopup(false);
        }
    }
    getCurrSelectItemIndex(data) {
        for (let i = 0; i < data.length; i++) {
            if (data[i].type == 'active') {
                return i;
            }
        }
        return null;
    }
    togglePopup(isShow) {
        this.setState({
            visible: isShow
        });
    }
    clickItem(index, value) {
        const { onChange, data } = this.props;
        // onChange
        if (onChange) {
            if (value) {
                onChange(Object.assign({ key: index }, data[index], { value }));
            }
            else {
                onChange(Object.assign({ key: index }, data[index]));
            }
        }
    }
    createTitle(title) {
        const { prefixCls } = this.props;
        return (<div className={`${prefixCls}-header`}>
                <h5>{title}</h5>
            </div>);
    }
    createContent(data) {
        const { prefixCls } = this.props;
        return (<div className={`${prefixCls}-body`}>
                <List>
                    {data.map((item, key) => {
            const classname = classNames(`${prefixCls}-item`, {
                [`${prefixCls}-item-disabled`]: item.type == 'disabled',
                [`${prefixCls}-item-active`]: item.type == 'active'
            });
            return (<List.ListItem onClick={() => {
                if (item.type != 'disabled') {
                    if (item.click && isFunction(item.click)) {
                        item.click(item, key, (value) => {
                            this.clickItem(key, value);
                        });
                    }
                    else {
                        this.clickItem(key);
                    }
                }
            }} key={key} title={<div className={classname}>{item.text}</div>} extra={item.type == 'active' ? <Icon style={{ width: '18px', height: '18px', color: '#2871F7' }} type='success_fill'/> : null}/>);
        })}
                </List>
            </div>);
    }
    render() {
        const { style, prefixCls, className, title, onClose, maskClose, data, bodyStyle } = this.props;
        const selectClassName = classNames(prefixCls, className);
        return (<Popup bodyStyle={Object.assign({ height: '60%' }, bodyStyle)} maskClose={maskClose} visible={this.state.visible} onClose={() => {
            this.setState({
                visible: false
            }, () => {
                onClose && onClose();
            });
        }}>
                <div style={style} className={selectClassName}>
                    {title && this.createTitle(title)}
                    {this.createContent(data)}
                </div>
            </Popup>);
    }
}
Select.defaultProps = {
    prefixCls: `${config.cls}-select`,
    className: '',
    style: {},
    isShow: false,
    onClose: null,
    title: null,
    data: [],
    onChange: null,
    maskClose: true
};



