import React, { PureComponent } from 'react';
import classNames from 'classnames';
import ListItme from './ListItem';
import config from '../../_util/config';
export default class List extends PureComponent {
    constructor(props) {
        super(props);
    }
    render() {
        const { style, prefixCls, children, className } = this.props;
        const cardClassName = classNames(prefixCls, className);
        return (<div style={style} className={cardClassName}>
                {children}
            </div>);
    }
}
List.defaultProps = {
    prefixCls: `${config.cls}-list`,
    className: '',
    style: {}
};
List.ListItem = ListItme;



