import React, { PureComponent } from 'react';
import classNames from 'classnames';
import config from '../../_util/config';
import { isFunction } from '../../_util/typeof';
export default class ListItem extends PureComponent {
    constructor(props) {
        super(props);
    }
    createHeader(title, extra) {
        const { prefixCls, align, extraClick } = this.props;
        const classname = classNames(`${prefixCls}-header`, `${prefixCls}-header-${align}`);
        return (<div className={classname}>
                <div className={`${prefixCls}-header-title`}>{title}</div>
                {extra && <div onClick={() => {
            if (extraClick && isFunction(extraClick)) {
                extraClick();
            }
        }} className={`${prefixCls}-header-extra`}>{extra}</div>}
            </div>);
    }
    createBody(children) {
        const { prefixCls } = this.props;
        return (<div className={`${prefixCls}-body`}>
                {children}
            </div>);
    }
    render() {
        const { style, prefixCls, children, className, title, extra, onClick } = this.props;
        const cardClassName = classNames(prefixCls, className);
        return (<div style={style} className={cardClassName} onClick={() => {
            if (onClick) {
                onClick();
            }
        }}>
                {title && this.createHeader(title, extra)}
                {children && this.createBody(children)}
            </div>);
    }
}
ListItem.defaultProps = {
    prefixCls: `${config.cls}-list-item`,
    className: '',
    style: {},
    title: null,
    extra: null,
    align: 'center',
    extraClick: null,
    onClick: null
};



