import React, { Children } from 'react';
import classnames from 'classnames';
import Step from './Step';
export default class Steps extends React.PureComponent {
    constructor(props) {
        super(props);
    }
    getStatus(current, index) {
        let status = 'wait';
        if (current < index) {
            status = 'wait';
        }
        else if (current > index) {
            status = 'finish';
        }
        else if (current === index) {
            status = 'active';
        }
        return status;
    }
    hasTag(children) {
        return React.Children.toArray(children).some((child) => {
            return !!child.props.tag;
        });
    }
    render() {
        const { className, direction, children, alignCenter, current, style } = this.props;
        const stepNumber = current;
        return (<div className={classnames('zds-steps', {
            'is-horizontal': direction === 'horizontal',
            'is-vertical': direction === 'vertical',
            [`${className}`]: !!className
        })} style={style}>
                <div className="zds-steps-scroller">
                    <div className="zds-steps-wrapper" style={{ paddingTop: this.hasTag(children) ? '30px' : 0 }}>
                        {Children.map(children, (child, index) => {
            if (React.isValidElement(child)) {
                const newProps = {
                    active: stepNumber === index,
                    alignCenter: alignCenter,
                    status: this.getStatus(stepNumber, index)
                };
                return React.cloneElement(child, newProps);
            }
            return null;
        })}
                    </div>
                </div>
            </div>);
    }
}
Steps.Step = Step;
Steps.defaultProps = {
    current: 0,
    direction: 'horizontal'
};



