import React, { PureComponent } from 'react';
import classNames from 'classnames';
import Icon from '../../Icon';
import config from '../../_util/config';
import { isFunction } from '../../_util/typeof';
export default class Prompt extends PureComponent {
    constructor(props) {
        super(props);
        this.handlerClickEvent = this.handlerClickEvent.bind(this);
    }
    handlerClickEvent() {
        const { onClick } = this.props;
        if (onClick && isFunction(onClick)) {
            onClick();
        }
    }
    getTheme(type) {
        switch (type) {
            case 'success':
                return "success-theme";
            case 'warn':
                return 'warn-theme';
            default:
                return "neutral-theme";
        }
    }
    renderTypeElement() {
        const { prefixCls, style, mode, contextText, lineClamp, type, children, className } = this.props;
        const theme = this.getTheme(type || '');
        const classname = classNames(prefixCls, className);
        switch (mode) {
            case 'info':
                return (<div className={`${classname} type-notice ${theme}`} onClick={this.handlerClickEvent} style={style}>
                        <Icon type='info_outline' className={`${prefixCls}-icon-info`}/>
                        <div className={`${prefixCls}-content`}>
                            {!!children ? children : <p style={{ 'WebkitLineClamp': lineClamp }} dangerouslySetInnerHTML={{ __html: contextText || '' }}></p>}
                        </div>
                    </div>);
            case 'click':
                return (<div className={`${classname} type-notice ${theme}`} onClick={this.handlerClickEvent} style={style}>
                        <div className={`${prefixCls}-content`}>
                            {!!children ? children : <p style={{ 'WebkitLineClamp': lineClamp }} dangerouslySetInnerHTML={{ __html: contextText || '' }}></p>}
                        </div>
                        <Icon type='arrows' className={`${prefixCls}-icon-right`}/>
                    </div>);
            case 'info*click':
                return (<div className={`${classname} type-notice ${theme}`} onClick={this.handlerClickEvent} style={style}>
                        <Icon type='info_outline' className={`${prefixCls}-icon-info`}/>
                        <div className={`${prefixCls}-content`}>
                            {!!children ? children : <p style={{ 'WebkitLineClamp': lineClamp }} dangerouslySetInnerHTML={{ __html: contextText || '' }}></p>}
                        </div>
                        <Icon type='arrows' className={`${prefixCls}-icon-right`}/>
                    </div>);
            default:
                return (<div className={`${classname} type-notice border-notice ${theme}`} onClick={this.handlerClickEvent} style={style}>
                        <div className={`${prefixCls}-content`}>
                            {!!children ? children : <p style={{ 'WebkitLineClamp': lineClamp }} dangerouslySetInnerHTML={{ __html: contextText || '' }}></p>}
                        </div>
                    </div>);
        }
    }
    render() {
        return this.renderTypeElement();
    }
}
Prompt.defaultProps = {
    prefixCls: `${config.cls}-prompt`,
    className: '',
    style: {},
    mode: 'defautl',
    type: 'defautl',
    lineClamp: 0,
    contextText: ''
};



