import React, { PureComponent } from 'react';
import classNames from 'classnames';
import config from '../../_util/config';
export default class SkeletonBox extends PureComponent {
    constructor(props) {
        super(props);
    }
    render() {
        const { style, prefixCls, className, justify, align, direction, children } = this.props;
        const selectClassName = classNames(prefixCls, className);
        return (<div style={Object.assign({}, style, { alignItems: align, justifyContent: justify, flexDirection: direction })} className={selectClassName}>
                {children}
            </div>);
    }
}
SkeletonBox.defaultProps = {
    prefixCls: `${config.cls}-skeleton-box`,
    className: '',
    style: {},
    justify: 'flex-start',
    align: 'flex-start',
    direction: "row"
};



